<template>
  <div>
    <h3>X组件</h3>
    <div>
      <div v-for="(item, index) in pros" :key="index">
        <h4>{{item.name}}</h4>
        <img class="auto-img" :src="item.url" alt="">
      </div>
    </div>
  </div>
</template>

<style scoped lang="less">
  .auto-img{
    width: 100%;
    display: block;
  }
</style>

<script>

  export default {

    name: 'x',

    data() {

      return {

        pros: null

      };

    },

    created() {
      let self = this;

      //截取查询参数
      // console.log('this.$route ==> ', this.$route);
      let queryParams = this.$route.query;

      this.http(queryParams).then(result => {
        self.pros = result;
      })

    },

    methods: {
      http(data) {

        return new Promise((resolve, reject) => {

          //发起ajax请求
          //创建ajax对象
          let xhr = new XMLHttpRequest();

          xhr.onreadystatechange = function () {
            if (this.readyState === 4 && this.status === 200) {

              //成功凝固
              resolve(this.response);
            }
          }

          let params = '';

          for (let key in data) {
            params += key + '=' + data[key] + '&';
          }

          params = params.slice(0, -1);

          xhr.open('GET', 'http://api.kangliuyong.com:9002/pros?' + params, true);

          //设置服务器响应的数据类型
          xhr.responseType = 'json';

          xhr.send(null);

        })

      }
    }

  }

</script>